body {
  margin: 0;
  padding: 0;
}

body {
  background: #216C2A;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #216C2A, #216C2A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-attachment: fixed;
  width: 100%;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
}

.main {
  height: 200vh;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80vw;
  margin: 0 auto;
  border-radius: 3px;
  background: #F5F5F5;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 3em;
  margin-bottom: 3em;
}

.header-container {
  width: 100%;
  background: #bdc3c7;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #2c3e50, #bdc3c7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  border-bottom: 1px solid #202020;
  text-align: center;
  height: 15vh;
}

.header-container h1 {
    color: black;
    opacity: 0.8;
    padding-top: 2vh;
    font-size: 4vw;
    font-weight: bold;
    font-family: Baskerville, "Baskerville Old Face", "Hoefler Text", Garamond, "Times New Roman", serif;
    font-style: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    color: black;
}

.form {
  display: -webkit-flex;
  display: flex;
  margin-top: 1vh;
  margin-left: 1vw;
}

#main-button {
  margin-left: 1vw;
  height: 5vh;
  width: 18vw;
  font-size: 1.1vw;
  margin-right: 1vw;
}

.display {
  text-align: center;
}

.display hr {
  width: 85%;
  border-top: 1px solid silver;
}

.analysis-title {
  margin: 2vw;
  font-size: 3vw;
}

#profitability-title {
  padding: 2vh;
  font-size: 2.5vw;
}

.profitability-ratios {
  font-size: 1.5vw;
}

#liquidity-title {
  font-size: 2.5vw;
}

.liquidity-ratios {
  font-size: 1.5vw;
}

#solvency-title {
  font-size: 2.5vw;
}

.solvency-ratios {
  font-size: 1.5vw;
}

#market-title {
  font-size: 2.5vw;
}

.market-ratios {
  font-size: 1.5vw;
}

